<template>
  <table class="table">
    <thead>
      <tr>
        <th>
          <label class="form-checkbox">
            <input
              type="checkbox"
              v-model="checkedB"
              @click="select"
              @change="$emit('update:listfilter', user.collection)"
              :style="{ width: '10px', height: '10px' }"
            />
            <i class="form-icon"></i>
          </label>
        </th>
        <th>Select All</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in this.list" v-bind:key="item">
        <td>
          <label class="form-checkbox">
            <input
              type="checkbox"
              :value="item"
              @change="$emit('update:listfilter', user.collection)"
              v-model="user.collection"
              :style="{ width: '10px', height: '10px' }"
            />
            <i class="form-icon"></i>
          </label>
        </td>
        <td style="text-align: left">
          {{ item }}
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: "photoGallery1",
  props: {
    list: Array,
    listfilter: Array,
  },
  data() {
    return {
      user: {
        collection: [],
      },
      checkedB: false,
    };
  },
  methods: {
    select() {
      this.user.collection = [];
      if (!this.checkedB) {
        for (let i in this.list) {
          this.user.collection.push(this.list[i]);
        }
      }
    },
  },
};
</script>

<style scoped>
.table {
  font-size: 0.8rem;
}
table,
th,
td {
  height: 5px;
}
</style>
