<template>
  <div class="about">
    <div class="Container" @keyup.left="countItDown()" @keyup.right="countIt()">
      <div id="Container">
        <div id="contact1">
          <!-- <b> {{ selectedCategory }}</b> -->
        </div>
        <br />
        <div class="sidebar" v-if="this.filteredItems.length">
          <!-- <hr /> -->
          <div class="box">
            <form id="yerp">
              <!-- <label>
                  <select
                    name="category"
                    id="category"
                    v-model="selectedCategory"
                    @change="countSet()"
                  >
                    <option
                      v-for="key in this.filteredFinall"
                      v-bind:value="key.name"
                      :key="key.name"
                    >
                      {{ key.name }}
                    </option>
                  </select>
                </label> -->

              <br />
              <!-- <Transition name="fade"> -->
              <transition-group tag="ul" class="content__list" name="company">
                <div
                  id="seriesList"
                  v-for="(r, i) in this.seriesKeys"
                  @click="count2set(i)"
                  :key="i"
                  :class="i === count2 ? 'active1' : 'not-active1'"
                >
                  {{ r }}
                </div>
              </transition-group>
              <!-- </Transition> -->
              <!-- <div
                  id="seriesN"
                  v-for="key in this.filteredFinall"
                  v-bind:value="key.name"
                  :key="key.name"
                >
                  {{ key.name }}
                </div> -->
              <!-- <p>Selected Category: {{ selectedCategory }}</p> -->
            </form>
          </div>
          <div class="box">
            <ImageItem
              styles="parent-style5"
              class="image__item"
              :source="
                require('@/assets/' +
                  this.filteredItems[this.count].id +
                  '.jpg')
              "
              alt="random image"
            />
          </div>
          <div class="box">
            {{ this.filteredItems[this.count].id }}***
            {{ this.filteredItems[this.count].title }} <br />
            {{ this.filteredItems[this.count].medium }}-
            {{ this.filteredItems[this.count].support }} -
            {{ this.filteredItems[this.count].year }} <br />

            {{ this.filteredItems[this.count].dimension }}
          </div>
        </div>
        <div>
          <button id="subsec" v-on:click="countItDown()">
            <div class="arrow2"></div>
          </button>
          <button id="subsec" v-on:click="countIt()">
            <div class="arrow1"></div>
          </button>
        </div>
        <div
          style="
            width: 100%;
            height: 200px;

            display: flex;
            justify-content: space-evenly;
            align-items: center;
          "
        >
          <div
            id="bottomDisplay"
            v-for="(key, index) in this.filteredItems"
            v-bind:value="key.name"
            :key="key.name"
            :class="index === count ? 'active' : 'not-active'"
            @click="selectIt(index)"
          >
            <ImageItem
              styles="parent-style6"
              class="image__item"
              :source="require('@/assets/' + key.id + '.jpg')"
              alt="random image"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ImageItem from "@/components/ImageItem.vue";
export default {
  name: "WorksOnCanvas",
  components: { ImageItem },
  props: {
    inventory: Array,
    listOfSupports: Array,
    listOfMediums: Array,
    listOfSeries: Array,
    arr: Array,
    listOfRed: Array,
    filteredFinal: Array,
    filteredFinall: Array,
    popInv: Array,
    filteredFinalll: Array,
  },
  data() {
    return {
      selectedCategory: "",
      selectedItem: "",
      inv: [],
      ok: false,
      count: 0,
      count2: 0,
      isActive: false,
      array1: [],
    };
  },
  methods: {
    count2set(i) {
      this.count2 = i;
      this.count = 0;
      return (this.selectedCategory = this.seriesKeys[this.count2]);
    },
    selectIt(g) {
      let value = g;
      this.count = value;
    },
    countIt() {
      if (this.count < this.filteredItems.length - 1) {
        return this.count++;
      }
    },
    countItDown() {
      if (this.count > 0) {
        return this.count--;
      }
    },
    countSet() {
      return (this.count = 0);
    },
    seriesUp() {
      this.count2++;
      return (this.selectedCategory = this.seriesKeys[this.count2]);
    },
    seriesDown() {
      this.count2--;
      return (this.selectedCategory = this.seriesKeys[this.count2]);
    },
    callEvent() {
      console.log("Event called");
    },
  },
  computed: {
    cmFunc(dim) {
      let g = this.filteredItems[dim].dimension;
      return g;
    },
    seriesKeys() {
      return Array.from(new Set(this.filteredFinall.map((d) => d.name)));
    },

    filteredItems: function () {
      return this.filteredFinalll.filter(function (el) {
        return el.series === this.selectedCategory;
      }, this);
    },
    maxIndex() {
      return this.filteredItems.length;
    },
    uIndex() {
      return 4;
    },
  },
  created() {
    this.selectedCategory = this.seriesKeys[this.count2];
    var self = this;
    window.addEventListener("keyup", function (event) {
      if (event.key === "ArrowRight") {
        self.countIt();
      }
      if (event.key === "ArrowLeft") {
        self.countItDown();
      }
      if (event.key === "ArrowDown") {
        self.seriesUp();
        self.countSet();
      }
      if (event.key === "ArrowUp") {
        self.seriesDown();
        self.countSet();
      }
    });
  },
  destroyed() {
    window.removeEventListener("keyup", null);
  },
};
</script>
<style scoped>
