        {{ this.userFilter[this.count].id }}
      </div>
      <div
        :style="{
          'font-style': 'italic',
          'font-weight': 'lighter',
          'font-size': '.9em',
          'padding-left': '25%',
          'text-align': 'left',
          'padding-top': '35px',
        }"
      >
        {{ this.userFilter[this.count].title }},
        <!-- {{ this.userFilter[this.count].medium }},
        {{ this.userFilter[this.count].support }}, -->
        {{ this.userFilter[this.count].year }}
      </div>
    </div>

    <div class="boxFull">
      <div class="box" v-if="this.userFilter.length">
        <ImageItem
          styles="parent-style5"
          class="image__item"
          :source="
            require('@/assets/' + this.userFilter[this.count].id + '.jpg')
          "
          alt="random image"
        />
      </div>
    </div>
    <!-- <div
      id="bottomDisplay"
      v-for="(key, index) in this.filt"
      v-bind:value="key.name"
      :key="key.name"
      :class="index === count ? 'active' : 'not-active'"
      @click="selectIt(index)"
    >
      <ImageItem
        styles="parent-style6"
        class="image__item"
        :source="require('@/assets/' + key.id + '.jpg')"
        alt="random image"
      />
    </div> -->
    <div>
      <button id="subsec" v-on:click="countItDown()">
        <div class="arrow2"></div>
      </button>
      <button id="subsec" v-on:click="countIt()">
        <div class="arrow1"></div>
      </button>
    </div>
  </div>
</template>
