  },
  methods: {
    selectIt(g) {
      let value = g;
      this.count = value;
    },
    countIt() {
      if (this.count < this.userFilter.length - 1) {
        return this.count++;
      }
    },
    countItDown() {
      if (this.count > 0) {
        return this.count--;
      }
    },
  },
  computed: {
    userFilter() {
      return this[this.uFilterKey];
    },
    setAbrev() {
      let g;
      if (this.selectionL == "ToThePeopleOfTheWorld") {
        g = "TTPW";
      }
      if (this.selectionL == "Monochromes") {
        g = "Monochromes and Single Color Paintings";
      }
      if (this.selectionL == "CrosbyStreetStudio") {
        g = "Crosby Street , 1979-2014";
      }
      if (this.selectionL == "EarlyWork") {
        g = "Early Work";
      }
      if (this.selectionL == "Prints") {
        g = "Prints";
      }
      if (this.selectionL == "Sketchbooks") {
        g = "Sketchbooks";
      }
      return g;
    },
    filt: function () {
      return this.inventory.filter(function (el) {
        return el.series === this.setAbrev;
      }, this);
    },
    all() {
      return this.inventory;
    },
    ToThePeopleOfTheWorld() {
      return this.inventory.filter((item) => item.series === this.setAbrev);
    },
    Monochromes() {
      return this.inventory.filter((f) =>
        this.monochromes.some((item) => item === f.id)
      );
    },
    CrosbyStreetStudio() {
      return this.inventory.filter((f) =>
        this.crosbyStreetStudio.some((item) => item === f.id)
      );
    },
    WorksOnPaper() {
      return this.inventory.filter((f) =>
        this.worksOnPaper.some((item) => item === f.id)
      );
    },
    Prints() {
      return this.inventory.filter((f) =>
        this.prints.some((item) => item === f.id)
      );
    },
    EarlyWork() {
      return this.inventory.filter((f) =>
        this.earlyWork.some((item) => item === f.id)
      );
    },
    Rubbings() {
      return this.inventory.filter((f) =>
        this.rubbings.some((item) => item === f.id)
      );
    },
    Sketchbooks() {
      return this.inventory.filter((f) =>
        this.sketchbooks.some((item) => item === f.id)
      );
    },
  },
};
