<style scoped>
#low {
  padding-top: 150px;
}
.arrow2 {
  box-sizing: border-box;
  height: 1vw;
  width: 1vw;
  border-style: solid;
  border-color: black;
  border-width: 0px 1px 1px 0px;
  transform: rotate(135deg);
  transition: border-width 150ms ease-in-out;
}

.arrow1 {
  box-sizing: border-box;
  height: 1vw;
  width: 1vw;
  border-style: solid;
  border-color: black;
  border-width: 0px 1px 1px 0px;
  transform: rotate(315deg);
  transition: border-width 150ms ease-in-out;
}
#subsec:hover > .arrow2 {
  border-bottom-width: 4px;
  border-right-width: 4px;
}
#subsec:hover > .arrow1 {
  border-bottom-width: 4px;
  border-right-width: 4px;
}
#subsec {
  background-color: white;
  border: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}
.box {
  display: block;
  margin-top: 1%;
  margin-left: auto;
  margin-right: auto;
  /* margin-bottom: 5%; */
}
.boxFull {
  height: calc(100vh-170px);
  display: flex;
  justify-content: center;
}
#TitleSub {
  font-size: 2em;
  padding-top: 30px;
  padding-bottom: 35px;
  text-align: left;
  padding-left: 10%;
}
#CountOf {
  font-size: 1.2em;
  text-align: left;
  padding-left: 15%;
}
</style>
