    align-content: flex-start;
  }
  .container {
    flex-wrap: wrap-reverse;
  }
  .container #frame {
    position: fixed;
    display: absolute;
    left: 0;
    right: 0;
    top: 10vh;
    max-width: 55vw;

    margin-top: 25vh;
    margin-left: auto;
    margin-right: auto;
    z-index: 15;
    background-color: white;
  }
  .gallery .snip {
    padding: 0px;
    background-color: blue;
  }
}
@media only screen and (max-width: 550px) {
