<script>
import ImageItem from "@/components/ImageItem.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "WorksOnCanvas",
  components: {
    ImageItem,
    Footer,
  },
  props: {
    inventory: Array,
    listOfSupports: Array,
    listOfMediums: Array,
    listOfSeries: Array,
    arr: Array,
    listOfRed: Array,
    filteredFinal: Array,
    filteredFinall: Array,
    popInv: Array,
    filteredFinalll: Array,
  },
  data() {
    return {
      selectedCategory: "",
      selectedItem: "",
      inv: [],
      ok: false,
      count: 0,
      count2: 0,
      isActive: false,
      array1: [],
      isOpen: false,
      tree: {
        name: "index",
        children: [
          {
            name: "post1",
            children: [{ name: "subject1" }, { name: "subject2" }],
          },
          {
            name: "post2",
            children: [{ name: "subject3" }, { name: "subject4" }],
          },
        ],
      },
    };
  },
  methods: {
    count2set(i) {
      this.count2 = i;
      this.count = 0;
      return (this.selectedCategory = this.seriesKeys[this.count2]);
    },
    selectIt(g) {
      let value = g;
      this.count = value;
    },
    countIt() {
      if (this.count < this.filteredItems.length - 1) {
        return this.count++;
      }
    },
    countItDown() {
      if (this.count > 0) {
        return this.count--;
      }
    },
    countSet() {
      return (this.count = 0);
    },
    seriesUp() {
      this.count2++;
      return (this.selectedCategory = this.seriesKeys[this.count2]);
    },
    seriesDown() {
      this.count2--;
      return (this.selectedCategory = this.seriesKeys[this.count2]);
    },
    callEvent() {
      console.log("Event called");
    },
  },
  computed: {
    seriesKeys() {
      return Array.from(new Set(this.filteredFinall.map((d) => d.name)));
    },

    filteredItems: function () {
      return this.filteredFinalll.filter(function (el) {
        return el.series === this.selectedCategory;
      }, this);
    },
    maxIndex() {
      return this.filteredItems.length;
    },
    uIndex() {
      return 4;
    },
  },
  mounted() {},
  created() {
    this.selectedCategory = this.seriesKeys[this.count2];
  },
  destroyed() {},
};
</script>
